import React from 'react'
import PropTypes from 'prop-types'
import BEMHelper from 'react-bem-helper'

const classes = new BEMHelper({
  name: 'slot',
  prefix: 'c-',
})

const Slot = ({ collection }) => {
  console.log(collection)
  return <div {...classes('')} />
}

Slot.propTypes = {
  collection: PropTypes.array,
}

Slot.defaultProps = {
  cssModfier: '',
}

export default Slot



